import React, { useState } from "react";
import { Category } from "../molecules";
import { Collapse } from "../organisms";

export default function CategoryList({ categories, category, onChange }) {
  const total = categories.reduce((a, b) => a + b.total, 0);
  const [cat, setCat] = useState(true);

  return (
    <div className="col-md-3">
      {/* <div className="card sticky-md-top" style={{ top: "75px" }}> */}
      <div className="card sticky-md-top mt-4">
        <div className="card-header text-dark" onClick={() => setCat(!cat)}>
          Categorías
        </div>
        <Collapse classNames="card-body p-0" toggle={cat} category={category}>
          <ul className="list-group list-group-flush">
            <Category
              category={category}
              id={""}
              onChange={onChange}
              name="Todos"
              total={total}
            />
            {categories.map((c) => {
              return (
                <Category
                  key={c.id}
                  category={category}
                  {...c}
                  onChange={onChange}
                />
              );
            })}
          </ul>
        </Collapse>
      </div>
    </div>
  );
}
